import "./index";
import "../scss/home.scss";

//=======MAIN BANNER========//
const prevBannerBtn = document.getElementById("banner-prev-btn");
const nextBannerBtn = document.getElementById("banner-next-btn");
const banner1 = document.getElementById("banner1");
const banner2 = document.getElementById("banner2");
const banner3 = document.getElementById("banner3");

//========BANNER SLIDE======//
const slides = [banner1, banner2, banner3];
let newIndex = 2;

nextBannerBtn.addEventListener("click", function slideUp() {
  for (let i = 0; i < slides.length; i++) {
    slides[i].style.opacity = 0;
  }
  newIndex++;
  if (newIndex >= slides.length) {
    newIndex = 0;
  }
  slides[newIndex].style.opacity = 1;
});

prevBannerBtn.addEventListener("click", function slideDown() {
  for (let i = 0; i < slides.length; i++) {
    slides[i].style.opacity = 0;
  }
  newIndex--;
  if (newIndex < 0) {
    newIndex = slides.length - 1;
  }
  slides[newIndex].style.opacity = 1;
});
//========BANNER SLIDE======//
