//========CARDs SLIDE DESKTOP======//
const cardWrapper = document.getElementById("card-wrapper");
const left = document.getElementById("left-card-slide-btn");
const right = document.getElementById("right-card-slide-btn");

right.addEventListener("click", () => {
  cardWrapper.scrollBy({
    left: 300,
    behavior: "smooth",
  });
});
left.addEventListener("click", () => {
  cardWrapper.scrollBy({
    left: -300,
    behavior: "smooth",
  });
});

