//=====BURGER MENU TOGGLE=====//
const closeBurgerBtn = document.getElementById("close-burger-btn");
const openBurgerBtn = document.getElementById("open-burger-btn");

openBurgerBtn.addEventListener("click", function toggleOnBurger() {
  const burgerMenu = document.getElementById("burger-menu");
  if ((burgerMenu.style.display = "none")) {
    burgerMenu.style.display = "flex";
    burgerIcon.style.display = "none";
    console.log("test");
  }
});
closeBurgerBtn.addEventListener("click", function toggleOffBurger() {
  const burgerMenu = document.getElementById("burger-menu");
  burgerMenu.style.display = "none";
  burgerIcon.style.display = "block";
});
//===========END============//
