  height: 0.1rem;\r\n        width: 100%;\r\n        transition: transform ease-in-out 350ms;\r\n        transform: scale(0);\r\n      }\r\n      a:hover::before {\r\n        transform: scale(1);\r\n      }\r\n    }\r\n    .nav-login-container {\r\n      flex-grow: 0;\r\n      display: flex;\r\n      justify-content: right;\r\n      align-items: center;\r\n      .button {\r\n        border-radius: 10px;\r\n        margin-left: 0.7rem;\r\n        font-size: 1.2rem;\r\n        width: 6rem;\r\n        background-color: c.$darkDetails;\r\n        height: 2rem;\r\n        box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.534);\r\n      }\r\n      .button:hover {\r\n        background-color: c.$lightDetails;\r\n      }\r\n    }\r\n  }\r\n  //=======SEARCH BAR======//\r\n  .search-container {\r\n    background-color: c.$fontMain;\r\n    padding: 0.4rem 0.3rem;\r\n    #search-bar {\r\n      width: 60%; ////////\r\n    }\r\n    #search-btn {\r\n      width: 5%;\r\n      margin-right: 1rem;\r\n    }\r\n    .cart-btn-search {\r\n      width: 10%;\r\n      height: 2.5rem;\r\n      margin-right: -2rem;\r\n      font-size: 1.5rem;\r\n      display: flex;\r\n      justify-content: center;\r\n      align-items: center;\r\n      .cart-img-search {\r\n        margin-left: 0.6rem;\r\n        background-image: url(../assets/icons/cartIcon.png);\r\n        background-position: right;\r\n        background-size: 100%;\r\n        background-repeat: no-repeat;\r\n        width: 30px;\r\n        height: 30px;\r\n      }\r\n    }\r\n  }\r\n  //========== FOOTER ========//\r\n  .footer-container {\r\n    font-size: 1.5rem;\r\n    justify-content: center;\r\n    gap: 15rem;\r\n  }\r\n  .footer-contacts-list {\r\n    .fc-link {\r\n      img {\r\n        height: 1.5rem;\r\n        margin-right: 0.7rem;\r\n      }\r\n    }\r\n  }\r\n}\r\n"],"sourceRoot":""}]);
